import React, { useState } from "react";
import styled from "styled-components";
import emailjs from "emailjs-com";

function ContactComponent() {
  const [value, setValue] = useState("");

  const handleChange = (event) => {
    setValue(event.target.value);
  };

  function sendEmail(e) {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_wuobvuk",
        "template_tp4ibp6",
        e.target,
        "user_e60Qd8S4i31lPRJ320hDm"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset();
    setValue("");
  }

  return (
    <ContactContainer>
      <aside style={asideContactStyle}>
        <strong>
          <p>Mail : robinlebonphotographe@gmail.com</p>

          <p>TEL : 0692 74 22 83 </p>
        </strong>
      </aside>
      <Form>
        <form onSubmit={sendEmail}>
          <h3>Remplir le formulaire </h3>
          <div>
            <label>Adresse mail * </label>
            <input
              name="email"
              type="email"
              placeholder="john@mail.com"
              autoComplete="email"
              aria-required="true"
              value={value}
              onChange={handleChange}
            />
          </div>
          <div>
            <label>Sujet * </label>
            <input
              type="text"
              placeholder="sujet"
              name="sujet"
              aria-required="true"
            />
          </div>
          <div>
            <label>Message * </label>
            <textarea
              name="message"
              id=""
              cols="30"
              rows="10"
              aria-required="true"
              placeholder="message"
            ></textarea>
          </div>
          <button disabled={!value} type="submit" value="submit">
            <strong> Submit </strong>
          </button>
        </form>
      </Form>
    </ContactContainer>
  );
}

export default ContactComponent;

const ContactContainer = styled.section`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 95%;
  margin: 100px 0 74px 0;
`;

const asideContactStyle = {
  minWidth: "20%",
  textDecoration: "none",
  color: "black",
  margin: "0 30px",
  fontFamily: "'Poppins', sans-serif",
  fontSize: "0.8rem",
};

const Form = styled.div`
  font-family: "Poppins", sans-serif;
  font-size: 0.8rem;
  form {
    display: flex;
    flex-direction: column;
    width: 30vw;
    min-width: 250px;
    div {
      display: flex;
      flex-direction: column;
      margin: 20px;
      label {
      }
      input {
        padding: 12px;
        margin: 6px 0 4px;
        border: 1px solid #ccc;
        background: #fafafa;
        font-size: 12px;
        border-radius: 2px;
        max-width: 100%;
        min-width: 250px;
      }
      textarea {
        resize: vertical;
        min-height: 100px;
        max-width: 100%;
        min-width: 250px;
        overflow: auto;
      }
    }
    button {
      color: black;
      border: 2px solid rgb(25, 25, 25);
      padding: 1em 2.5em;
      background: white;
      font-family: "Poppins", sans-serif;
      width: 150px;
      text-align: center;
      text-transform: uppercase;
      margin-left: 20px;
      cursor: pointer;
      &:hover {
        background: black;
        color: white;
      }
    }
  }
`;
